@import '../../styles/vars.scss';

.list {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__search {
        display: flex;
        gap: 10px;
        flex-direction: column;
        margin-top: 50px;
        border: 3px solid $logo-bckg;
        background: $search-bckg;

        &-one {
            display: flex;
            gap: 57px;
            padding: 10px;
        }

        &-two {
            display: flex;
            gap: 20px;
            padding: 10px;
        }
    }

    &__content {
        list-style-type: none;

        &-item {
            display: flex;
            justify-content: space-between;
            margin: 10px;
            border: 3px solid $repo-border;
            border-radius: 7px;
            padding: 7px;
            background: $repo-bckg;

            &>p {
                margin-left: 20px;
            }

            &>a {
                text-decoration: none;
                color: $logo-bckg;

                &:hover {
                    color: $hovered-repo;
                }
            }
        }
    }
}

.searching {
    display: flex;
    gap: 20px;

    &>h3 {
        transition: color 0.5s;

        &:hover {
            color: $exit-menu;
            cursor: pointer;
        }
    }
}